.numbers{
    background: #95e1d3;
    padding: 65px;
    position: relative;
    .marti{
        border-left: 2px solid #b5eae0;
        border-right: 2px solid #b5eae0;
        .md-block({
           border: none; 
        });
    }
    .num{
        margin-top: 20px;
        .md-block({
            margin-top: 20px;
        });
        text-align: center;
        h2{
            font-size: 72px;
            color: @white;
        }
        p{
            font-size: 17px;
            color: @white;
            margin-top: 15px;
        }
    }
}