.out-team{
    padding-top: 80px;
    .kalibra{
        margin-top: 50px;
    }
    .items-log{
        position: relative;
        cursor: pointer;
        img{
            .md-block({
                width: 100%;
            });
            .sm-block({
                width: 100%;
            });
        }
        &:hover .solid{
            opacity: 1;
            transform: translateY(-15px) translateX(-15px);
        }
        .solid{
            opacity: 0;
            transition: 500ms;
            position: absolute;
            top: 0;
            left: 0;
            background: url("../img/team1.png")no-repeat center center;
            background-size: cover;
            width: 100%;
            height: 100%;
            text-align: center;
            display: flex;
            justify-content: center;
            align-items: center;
            .sm-block({
               width: 100%;
               height: 100% !important; 
            });
            a{
                color: @red;
                font-size: 30px;
                padding: 14px;
                background: #fce38a;
                cursor: pointer;
                &:hover{
                    background: @red;
                    color: @white;
                }
            }
            .fa{
               
            }
        }
    }
   .text-team{
    .md-block({
        margin-bottom: 30px;
    });
       text-align: center;
       h6{
           font-size: 14px;
           margin-top: 25px;
           color: black;
           font-family: "Montserrat";
           font-weight: 500;
       }
       p{
           margin-top: 15px;
           font-weight: 300;
           font-size: 15px;
           font-family: "Roboto";
           color: @p;
       }
   }
   
   .vidjeti{
       background: #f8f8f8;
       margin-top: 80px;
       padding: 55px 0;
   }
   .log-b{
       text-align: center;
       display: flex;
       justify-content: center;
       align-items: center;
   }
   .log-t{
       text-align: center;
       .md-block({
           margin-top: 25px;
       });
       .sm-block({
           margin-top: 35px;
       });
   }
   
   
   
}