.amazing{ 
    position: relative;
    background: linear-gradient(#FF512F, #F09819);
    .sena{
        position: absolute;
        bottom: 0;
        left: 0;
        opacity: 0.5;
        .sm-block({
           display: none; 
        });
        .para1{
            left: 10% !important;
            .media-block({
                left: 0% !important;
            },870px);
            .md-block({
                left: 0% !important;
              });
        }
        .para2{
            margin-top: 150px;
            .md-block({
              display: none !important;  
            });
        }
        .para3{
            .md-block({
              display: none !important;  
            });
        }
    }
    .text-about{
        margin-top: 110px;
        margin-bottom: 60px;
        h4{
            color: @white;
        }
        h3{
            color: @white;
        }
        .liniya{
            background: @white;
        }
    }
    .servis{
        display: flex;
        margin-bottom: 80px;
        .sm-block({
            text-align: center;
        });
    }
    .images{
        width: 50px;
        margin-right: 30px;
        .sm-block({
            display: none;
        });
    }
    .deck{
        .sm-block({
            text-align: center;
        });
        h4{
            font-family: "Montserrat";
            font-size: 14px;
            font-weight: 400;
            text-transform: uppercase;
            color: @white;
        }
        p{
            font-size: 16px;
            font-family: "Roboto";
            line-height: 25px;
            font-weight: 400;
            color: @white;
        }
    }
}