.people{
    padding-top: 100px;
    background: url("../img/work.jpg")no-repeat;
    background-attachment: fixed;
    width: 100%;
    padding-bottom: 80x;
    .text-about{
        margin-bottom: 70px;
    }
    .names{
        display: flex;
        margin-bottom: 50px;
    }
    .avatar{
        margin-right: 25px;
    }
    .text-name{
        h5{
            font-family: "Montserrat";
            font-size: 14px;
            font-weight: 400;
            color: @h3;
        }
        h6{
            font-family: "Roboto";
            font-size: 15px;
            font-weight: 300;
            margin-top: 15px;
        }
        p{
            line-height: 25px;
            margin-top: 45px;
            font-size: 15px;
            font-weight: 300;
            color: @p;
            font-family: "Roboto";
            &::before{
                content: "";
                width: 60px;
                height: 3px;
                background: @red;
                display: block;
                transform: translateY(-25px);
            }
        }
    }
}