.akardion{
    padding-top: 80px;
    padding-bottom: 80px;
    .photo{
        img{
            .lg-block({
               width: 100%; 
            });
        }
    }
    .near{
        margin-top: 50px;
    }
    .akardions{
        .play{
            padding: 16px 0 16px 13px;
            display: flex;
            align-items: center;
            border: 1px solid #cccccc;
            margin-top: 15px;
            img{
                margin-right: 13px;
            }
            h5{
                font-family: "Montserrat";
                font-size: 14px;
                font-weight: 400;
                text-transform: uppercase;
            }
            cursor: pointer;
            position: relative;
            transition: 500ms;
            &::after{
                content: "\f107";
                font-family: "FontAwesome";
                position: absolute;
                right: 15px;
                transition: 500ms; 
                top: 15px;
                color: #cccccc;
                font-size: 20px;
            }
        }
        .play.in{
            background: rgb(0, 153, 255);
            box-shadow: 0 4px 10px 0 grey;
            h5{
                color: @white;
            }
        }
        .in{
            &::after{
                transform: rotate(180deg);
                color: #cccccc;
            }
        };
        .panel{
            border: 1px solid grey;
            margin-top: 10px;
            p{
                line-height: 24px;
                color: @p;
                font-size: 15px;
                font-weight: 300;
                font-family: "Roboto";
                margin: 10px;
            }
        }
    }
}