.blog{
    padding: 100px 0;
    .sova-two{
        margin-top: 48px;
    }
    .ofis{
        img{
            position: relative;
        }
        .num{
            padding: 8px;
            background: #95e1d3;
            transform: translateY(-65px) translateX(-10px);
            font-size: 25px;
            font-weight: bold;
            color: @white;
            position: absolute;
            .sm-block({
               display: none; 
            });
            .jun{
                font-size: 18px;
                font-weight: normal;
            }
        }
        h5{
            font-size: 14px;
            font-weight: 400;
            font-family: "Montserrat";
            color: @h3;
            margin-top: 15px;
            text-transform: uppercase;
        }
        p{
            font-family: "Roboto";
            font-size: 15px;
            color: @p;
            font-weight: 400;
            line-height: 25px;
            margin-top: 20px;
            border-bottom: 1px solid #e5e5e5;
            padding-bottom: 18px;
        }
        .coment{
            margin-top: 18px;
            .fa{
                color: #95e1d3;
                font-size: 15px;
                margin-right: 5px;
            }
        }
    }
}