.header-1 {
    .bg("../img/bg-header.jpg");
    position: relative;
    padding-top: 40px;
    height: 100vh;
    .menu-top {
        a{
            .sm-block({
                 display: none;
            });
        }
        
        display: flex;
        position: relative;
        justify-content: space-between;
        a {
            color: @white;
            font-weight: 600;
            font-size: 30px;
            font-family: "Montserrat";
        }
        .gamb {
            display: none;
            .media-block( {
                display: block;
            }
            , 991px);
        }
        .menu-gamb {
            display: none;
            .media-block( {
                display: block;
            }
            , 991px);
        }
        .items {
            display: flex;
            .media-block( {
                display: none;
            }
            , 991px);
            .links {
                margin: 0 20px;
                font-family: "Montserrat";
                font-size: 14px;
                text-transform: uppercase;
                color: @white;
                font-weight: 600;
                position: relative;
                &:hover {
                    color: @jel;
                }
                &:hover::after {
                    opacity: 1;
                    width: 100%;
                }
                &::after {
                    content: "";
                    transition: 500ms;
                    opacity: 0;
                    left: 0;
                    position: absolute;
                    display: block;
                    width: 30%;
                    height: 3px;
                    background: @jel;
                    transform: translateY(5px);
                }
                .fa {
                    font-size: 18px;
                }
            }
        }
        .menu-gamb {
            p {
                line-height: 1.5;
            }
            .nav-trigger {
            position: fixed;
            width: 110px;
            height: 98px;
            right: 0px;
            top: 0px;
            cursor: pointer;
            z-index: 200;
            transform-origin: 110px 0px;
            transform: scale(0.6);
            transition: background 0.3s linear;
            .sm-block({
                width: 80px;
                height: 70px;  
            });
            }
            .nav-trigger.open {
                margin-top: 20px;
                margin-right: 20px;
                background: linear-gradient(#F09819,#EDDE5D);
            }
            .nav-trigger.close {
                background: transparent;
            }
            .line {
                display: block;
                position: absolute;
                width: 80px;
                height: 8px;
                background: #fff;
                left: 15px;
                transform-origin: 0px 4px;
                transition: opacity 0.3s cubic-bezier(0.645, 0.045, 0.355, 1) 0s, transform 0.24s cubic-bezier(0.645, 0.045, 0.355, 1) 0s;
                .sm-block({
                    width: 50px;
                    height: 5px;  
                });
            }
            .line:nth-child(1) {
                top: 16px;
            }
            .line:nth-child(2) {
                top: 44px;
                .sm-block({
                    top: 30px;
                });
            }
            .line:nth-child(3) {
                top: 72px;
                .sm-block({
                    top: 45px;
                });
            }
            .close .line:nth-child(1) {
                transform: translateX(11px) rotate(45deg);
            }
            .close .line:nth-child(2) {
                opacity: 0;
            }
            .close .line:nth-child(3) {
                transform: translateX(11px) rotate(-45deg);
            }
            nav {
                width: 100%;
                height: 100%;
                position: fixed;
                z-index: 100;
            }
            nav.out {
                pointer-events: none;
            }
            nav.in {
                pointer-events: auto;
            }
            nav ul,
            li {
                display: block;
                padding: 0;
                margin: 0;
                width: 100%;
                cursor: pointer;
            }
            nav ul {
                height: 100%;
                width: 100%;
                position: fixed;
                top: 0;
                left: 0;
            }
            nav li {
                height: 20%;
                position: relative;
                background: #222;
                color: #fff;
                font-family: "Patua One", cursive;
            }
            nav li:hover {
                background: #364e41;
            }
            nav.in li:nth-child(1) {
                transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.1s, opacity 0.5s linear 0.1s;
            }
            nav.in li:nth-child(2) {
                transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.2s, opacity 0.5s linear 0.2s;
            }
            nav.in li:nth-child(3) {
                transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.3s, opacity 0.5s linear 0.3s;
            }
            nav.in li:nth-child(4) {
                transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.4s, opacity 0.5s linear 0.4s;
            }
            nav.in li:nth-child(5) {
                transition: transform 0.5s cubic-bezier(0.215, 0.61, 0.355, 1) 0.5s, opacity 0.5s linear 0.5s;
            }
            nav.out li:nth-child(5) {
                transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0s, opacity 0.3s linear 0s;
            }
            nav.out li:nth-child(4) {
                transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.05s, opacity 0.3s linear 0.05s;
            }
            nav.out li:nth-child(3) {
                transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.1s, opacity 0.3s linear 0.1s;
            }
            nav.out li:nth-child(2) {
                transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.15s, opacity 0.3s linear 0.15s;
            }
            nav.out li:nth-child(1) {
                transition: transform 0.3s cubic-bezier(0.55, 0.055, 0.675, 0.19) 0.2s, opacity 0.3s linear 0.2s;
            }
            nav.out li {
                transform: translateY(300%);
                opacity: 0;
            }
            nav.in li {
                transform: translateY(0);
                opacity: 1;
            }
            .nav-label {
                position: absolute;
                width: 100%;
                left: 0;
                text-align: center;
                height: 16px;
                top: 50%;
                margin-top: -8px;
                text-transform: uppercase;
            }
            .header {
                background: url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/53148/bg.jpg);
                background-size: cover;
                position: relative;
                width: 100%;
                min-height: 100%;
                top: 0px;
            }
            .header h1 {
                width: 100%;
                color: #fff;
                position: absolute;
                margin: 0;
                font-family: "Patua One", cursive;
                text-align: center;
                display: block;
                top: 50%;
            }
            .content {
                box-sizing: border-box;
                max-width: 600px;
                padding: 20px;
                margin: 0 auto;
            }
            .credit {
                color: #2c9d5f;
            }
            .credit a {
                color: #364e41;
            }
        }
    }
    .text-center {
        position: absolute;
        left: 0;
        right: 0;
        text-align: center;
        width: 100%;
        top: 20%;
        h2 {
            font-family: "Kaushan Script";
            color: @white;
            font-size: 62px;
            margin-bottom: 30px;
            .sm-block({
               font-size: 55px; 
            });
            .media-block({
                font-size: 40px;
            },450px);
        }
        h1 {
            color: @white;
            font-family: "Montserrat";
            font-size: 100px;
            font-weight: 700;
            .sm-block({
                font-size: 75px; 
             });
             .media-block({
                font-size: 55px;
            },450px);
        }
        .btn {
            margin-top: 80px;
            border: 3px solid @white;
            padding: 12px 30px;
            font-family: "Montserrat";
            font-weight: 600;
            background: transparent;
            cursor: pointer;
            outline: none;
            font-size: 14px;
            text-transform: uppercase;
            transition: 500ms;
            &:hover {
                background: rgba(247, 162, 5, 0.541);
                border: 3px solid rgb(255, 225, 0);
                box-shadow: 0 2px 25px 7px rgb(255, 255, 255);
            }
            &::before {
                content: "";
                width: 60px;
                height: 3px;
                background: @white;
                display: block;
                transform: translateY(-50px) translateX(20px);
            }
        }
    }
}