.end{
    padding: 60px 0;
    .mogo{
        h4{
            font-family: "Montserrat";
            font-weight: 600;
            font-size: 46px;
            color: #cccccc;
        }
        p{
            font-family: "Roboto";
            font-size: 15px;
            color: @p;
            line-height: 24px;
        }
        h5{
            margin-top: 30px;
            font-size: 15px;
            font-family: "Montserrat";
            font-weight: 400;
            border-bottom: 1px solid #e5e5e5;
            padding-bottom: 15px;
        }
        .fonz{
            font-size: 20px;
            font-weight: 600;
            color: black;
        }
        .flow{
            margin-top: 15px;
            font-family: "Roboto";
            font-size: 15px;
            color: @p;
            display: block;
            margin-bottom: 25px;
            .fa{
                color: #95e1d3;
                font-size: 15px;
                margin-left: 10px;
            }
        }
        .inp{
            input{
                padding: 8px 0 8px 6px;
                font-size: 15px;
                font-family: "Roboto";
                font-weight: 300;
                color: @p;
                border: 1px solid #e7e7e7;
            }
        }
        .btn{
            font-size: 14px;
            text-transform: uppercase;
            color: @white;
            font-family: "Montserrat";
            font-weight: 600;
            padding: 8px 10px;
            background: #95e1d3;
            cursor: pointer;
            border: none;
            &:hover{
                box-shadow: 0 4px 20px 5px rgb(199, 198, 198);
            }
        }
    }
    h6{
        font-size: 14px;
        font-family: "Montserrat";
        font-weight: 300;
        color: #777777;
        text-transform: uppercase;
        margin-bottom: 40px;
    }
    .loremm{
          .sm-block({
           margin-top: 50px; 
        });
          .md-block({
           margin-top: 50px; 
        });
    }
    .lorem{
        display: flex;
        margin-top: 20px;    
    }
    .text-lorem{
        margin-left: 20px;
        display: flex;
        flex-direction: column;
        p{
            font-family: "Montserrat";
            font-size: 12px;
            color: @p;
            line-height: 20px;
        }
        h6{
            font-family: "Roboto";
            font-size: 13px;
            color: @p;
            margin-top: 10px;
        }
    }
    
    .thre-foot{
        .md-block({
            margin-top: 50px; 
        });
        .sm-block({
           margin-top: 20px; 
        });
        h5{
            font-family: "Roboto";
            font-size: 13px;
            color: black;
            font-weight: 300;
            margin-top: 20px;
        }
    }
}