.karusel{
    background: #f8f8f8;
    padding: 60px 0;
    .md-block({
       display: none; 
    });
    .gallery{
        width: 100%;
        display: flex;
        flex-direction: column;
        min-height: 200px;
        justify-content: center;
    }
    .text-line{
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .jon-dea{
        max-width: 100%;
        position: absolute;
        opacity: 0;
        display: flex;
        align-items: center;
        top: 0;
        margin-top: 20px;
        transition: 1000ms;
        h6{
            position: absolute;
            transform: translateX(267px) translateY(50px);
            font-family: "Kaushan Script";
            font-size: 24px;
            color: black;
            .md-block({
               display: none; 
            });
        }
        p{
            font-size: 24px;
            color: @p;
            line-height: 25px;
            margin-left: 50px;
            font-weight: 200;
            position: relative;
            &::after{
                content: "";
                display: block;
                width: 60px;
                height: 3px;
                background: @red;
                transform: translateY(26px);
            }
        }
    }
    .jon-dea.active{
        opacity: 1;
    }
    .prev{
        position: absolute;
        top: 40%;
        right: -50px;
        cursor: pointer;
        .fa{
            font-size: 45px;
            color: #cccccc;
        }
    }
    .next{
        position: absolute;
        top: 40%;
        left: -50px;
        cursor: pointer;
        .fa{
            font-size: 45px;
            color: #cccccc;
        }
    }
    
}