.what{
    background: url("../img/work.jpg")no-repeat center;
    background-attachment: fixed;
    min-height: 857px;
    width: 100%;
    background-size: cover;
    padding-top: 80px;
    position: relative;
    .phon{
        position: absolute;
        bottom: 0;
        .sm-block({
           top: 0;
           margin-top: 300px; 
        });
    };
    .iphone{
        position: relative;
    };
    .iphone{
        display: flex;
        justify-content: center;
    };
    .mobil-2{
        position: absolute;
        bottom: -10%;
        right: 0;
        transition: 500ms;
        cursor: pointer;
        &:hover{
           bottom: 0;
        }
        .sm-block({
           display: none; 
        });
    }
}