body{
    min-width: 320px;
    margin: 0;
    padding: 0;
}
a{
    text-decoration: none;
}
img{
    max-width: 100%;
    height: auto;
}
h4{
    font-size: 24px;
    color: @h3;
    font-family: "Kaushan Script";
    margin-bottom: 25px;
}
h3{
  font-family: "Montserrat";
  font-weight: 700;
  font-size: 30px;  
  color: @h3;
}
.text-about{
    text-align: center;
    h3{
        text-transform: uppercase;
    }
    .liniya{
        width: 60px;
        height: 3px;
        background: @red;
        display: inline-block;
        margin-top: 40px;
    }
    p{
        margin-top: 40px;
        color: @p;
        font-size: 15px;
        line-height: 20px;
    }
}


@import url("https://fonts.googleapis.com/css?family=Karla:400,700");
