.some{
    padding-top: 80px;
    .big-life{
        margin-top: 100px;
        overflow: hidden;
        box-sizing: border-box;
    }
    .slid{
        margin-top: 80px;
    }
    .life{
        margin: 0;
        padding: 0;
    }
    .life-1{
        position: relative;
        cursor: pointer;
        img{
            .sm-block({
                width: 100%;
            });
        }
        &:hover .cris{
            opacity: 1;
        }
        .cris{
            transition: 500ms;
            opacity: 0;
            position: absolute;
            top: 0%;
            left: 0%;
            background: url("../img/big4.jpg")no-repeat center;
            background-size: cover;
            width: 100%;
            height: 100%;
            text-align: center;
            justify-content: center;
            align-items: center;
            display: inline-flex;
            flex-direction: column;
            .fa{
                font-size: 30px;
                color: @white;
            }
            h6{
                font-size: 14px;
                color: @white;
                font-family: "Montserrat";
                font-weight: 400;
                text-transform: uppercase;
                margin-top: 15px;
            }
            p{
                font-size: 15px;
                font-family: "Roboto";
                color: @white;
                margin-top: 15px;
                font-weight: 300;
            }
        }
    }
    
    .galleryy{
        width: 100%;
        display: flex;
        flex-direction: column;
        min-height: 200px;
        justify-content: center;
        margin-bottom: 100px;
        .sm-block({
           display: none; 
        });
    }
    .text-line{
        position: relative;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
    }
    .jon-deaa{
        position: absolute;
        opacity: 0;
        display: flex;
        align-items: center;
        top: 0;
        margin-top: 20px;
        transition: 1000ms;
        h6{
            position: absolute;
            transform: translateX(350px) translateY(50px);
            font-family: "Kaushan Script";
            font-size: 24px;
            color: black;
            .md-block({
               display: none; 
            });
        }
        p{
            font-size: 24px;
            color: @p;
            line-height: 25px;
            margin-left: 50px;
            font-weight: 200;
            position: relative;
            &::after{
                content: "";
                display: block;
                width: 60px;
                height: 3px;
                background: @red;
                transform: translateY(26px);
            }
        }
    }
    .jon-deaa.kill{
        opacity: 1;
    }
    .prevv{
        position: absolute;
        top: 40%;
        right: -50px;
        cursor: pointer;
        .fa{
            font-size: 45px;
            color: #cccccc;
        }
    }
    .nextt{
        position: absolute;
        top: 40%;
        left: -50px;
        cursor: pointer;
        .fa{
            font-size: 45px;
            color: #cccccc;
        }
    }
    
}