.about{
    padding: 90px 0 100px 0;
    .text-about{
        text-align: center;
        h3{
            text-transform: uppercase;
        }
        .liniya{
            width: 60px;
            height: 3px;
            background: @red;
            display: inline-block;
            margin-top: 40px;
        }
        p{
            margin-top: 40px;
            color: @p;
            font-size: 15px;
            line-height: 20px;
        }
    }
    .slider-sova{
        margin-top: 60px;
        cursor: pointer;
        .membe{
            position: relative;
            &:hover .over{
                opacity: 1;
            }
            .over{
                position: absolute;
                top: 0;
                width: 100%;
                height: 100%;
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-items: center;
                background: url("../img/about11.png") center;
                opacity: 0;
                transition: 500ms;
                .fa{
                    font-size: 25px;
                    color: @white;
                }
                .sm-block({
                   display: none; 
                });
                p{
                    font-family: "fantasy";
                    font-size: 15px;
                    color: @white;
                    font-weight: 700;
                    text-transform: uppercase;
                    margin-top: 10px;
                }
            }
        }
    }
    
}